* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
html {
    font-family: 'Londrina Solid', cursive;
    font-family: 'Bungee Shade', cursive;
    font-size:62.5%;
}
body {
    background: #000000;
}
h1 {
    font:900 5.3rem "Roboto";
    color:#ffffff;
    text-align: center;
}
h2 {
    font:900 5rem "Roboto";
    color:#404040;
}
a {
    display: inline-block;
    text-decoration: none;
}
a.navLink:hover {
    text-decoration: underline;
  }
  .gridContainer {
    max-width: 1300px;
    margin: 0 auto;
  }
  .logo {
    width:20%;
    text-align: center;
    font:900 7rem "Londrina Solid";
    color:#fff;
    margin:5px;
  }
  li {
    margin:15px;
    list-style: none;
  }
/*----------------------------- header ---------------------------*/
.header {
    padding:30px;
}
.flex1 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.navUp .navList {
    display:flex;
    justify-content: space-around;
}
.navUp {
   margin:5px;
}
.navLink {
    font:900 2.5rem "Bungee Shade";
    color:#35503f;
}
.flex2 .row,
.flex2 .row {
    display: flex;
}
.news .article {
    width: 33.33%;
    height: 400px;
    box-shadow: 15px 3px 20px rgba(187, 187, 187,.85);
    margin: 15px;
    border-radius: 5%;
    background: #fff;
    //border: .2rem solid #595959bf;
    position: relative;
}
.news .article {
    width: 50%;
}
.row #music {
    background: url('../img/7Fkbb.jpg')center no-repeat;
    background-size:cover;
}
.article .linkArticle {
    position: absolute;
    width: 100%;
    height:100%;
}
.linkArticle .text {
    position: absolute;
    font-size: 3.5rem;
    margin-left:13px;
}


